import React, { useState, useEffect, useRef, useCallback } from 'react';
import {
  View,
  ScrollView,
  Text,
  Image,
  TextInput,
  Button
} from 'react-native-web';
import './App.css';
const {
  Configuration,
  // OpenAIApi
} = require("openai");

const prompt_starter = "The following is an online therapy session between Aurora, a compassionate therapist, and her client. Aura understands human behavior and mental disorders, and uses a wide variety of techniques to give thoughtful answers to her client."
const key_message = "Hey there! I'm excited to get started. First, can you enter the api key?"
const therapist_start_sequence = "Hi there, how about you tell me what brought you here today?"

const initialMessages = [
  buildMessage(
    key_message,
    false
  )
]

let apiKey = ""
let configuration = new Configuration({
  apiKey: apiKey,
});

function App() {
  const [messages, setMessages] = useState([]);
  const [isInitialLoad, setIsInitialLoad] = useState(true);

  const fetchGPTAnswer = useCallback(async (input) => {
    // fetch GPT answer
    console.log('going to ask gpt');
    console.log('prompt: ', createPrompt(messages));

    const requestOptions = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + String(configuration.apiKey)
      },
      body: JSON.stringify({
        'prompt': createPrompt(messages),
        'temperature': 0.9,
        'max_tokens': 150,
        'top_p': 1,
        'frequency_penalty': 0,
        'presence_penalty': 0.6,
        'stop': ['Aurora:','Client:'],
      })
    };
    fetch('https://api.openai.com/v1/engines/text-davinci-003/completions', requestOptions)
        .then(response => response.json())
        .then(data => {
          console.log(data);
          // build message
          const nextMessages = [
            ...messages,
            buildMessage(
              data.choices[0].text.trim(),
              false
            )
          ];
          // set messages
          setMessages(nextMessages);
    }).catch(err => {
      console.log(err);
          // build message
          console.log(err.message);
          const nextMessages = [
            ...messages,
            buildMessage(
              "Sorry my servers are overloaded right now - can you try again in a little bit?",
              false
            )
          ];
          // set messages
          setMessages(nextMessages);
    });


  }, [messages, setMessages]);


  useEffect(() => {
    if (isInitialLoad) {
      setIsInitialLoad(false);

      setTimeout(() => {
        setMessages(initialMessages);
      }, 2000);
    }

    const isLastMessageFromUser = getLastMessage(messages)?.isUser === true;
    if (isLastMessageFromUser) {
      console.log(apiKey);
      console.log(configuration);
      console.log(configuration.apiKey);
      console.log(apiKey.substring(0,1));
      console.log(apiKey.substring(0,1) === "sk");
      if (apiKey.substring(0,2) === "sk")  {
        fetchGPTAnswer(getLastMessage(messages).message);
      }
      else {
        // build message
        console.log('setting api key', getLastMessage(messages).message);
        apiKey = getLastMessage(messages).message

        console.log('configuring the ai', apiKey);
        configuration.apiKey = apiKey

        const nextMessages = [
          ...messages,
          buildMessage(
            "Thanks! Ok how about you tell me what brought you here today?",
            false
          )
        ];
        // set messages
        setMessages(nextMessages);
      }
    }
  }, [setMessages, messages, isInitialLoad, fetchGPTAnswer]);

  return (
    <Container>
      {/* <PhoneMask> */}
        <Header />
        <Messages messages={messages} />
        <Footer messages={messages} setMessages={setMessages} />
      {/* </PhoneMask> */}
   </Container>
  );
}

function Header() {
  return (
    <View style={{
      height: 70,
      // backgroundColor: '#F3F3F3',
      borderColor: '#999999',
      borderWidth: 1,
      justifyContent: 'center',
      alignItems: 'center',
    }}>
      {/* <Image
        style={{
          backgroundColor: '#9485CE',
          height: 50,
          width: 50,
          bordeRadius: 50,
        }}
      /> */}
      <Text style={{
        fontSize: 18,
        // fontWeight: 'bold',
      }}>
        Chatting with Aurora, your AI therapist
      </Text>
    </View>
  )
}

function Messages({ messages = [] }) {
  return (
    <ScrollView
      style={{
        paddingHorizontal: 20,
        paddingVertical: 10
      }}
      contentContainerStyle={{ flex: 1 }}
    >
      {messages.map((it, index) => (
        <Message
          key={index.toString()}
          message={it}
        />
      ))}
    </ScrollView>
  );
}

function Message({ message }) {
  const {
    message: string,
    isUser
  } = message;
  const backgroundColor = isUser ? '#D7EDFF' : '#ECEDF2';
  const maxWidth = '75%';
  const alignSelf = isUser ? 'flex-end' : 'flex-start'

  return (
    <View style={{
      backgroundColor,
      paddingTop: 4,
      paddingBottom: 5,
      paddingHorizontal: 12,
      marginBottom: 12,
      borderRadius: 15,
      maxWidth,
      alignSelf,
    }}>
      <Text style={{
        fontSize:16
      }}>{string}</Text>
    </View>
  )
}

function Footer({ messages = [], setMessages = () => {} }) {
  const ref = useRef();
  const backgroundColor = 'white';
  let activeMessage = '';

  return (
    <View style={{
      height: 35,
      backgroundColor,
      justifyContent: 'center',
      paddingHorizontal: 20,
      flexDirection: 'row',
      marginVertical:15,

    }}>
      {/* https://necolas.github.io/react-native-web/docs/text-input/ */}
      <TextInput
        ref={ref}
        placeholder="Type your message"
        placeholderTextColor="#C5C5C6"
        autoComplete="autocomplete"
        autoCorrect="on"
        autoCapitalize="sentences"
        onChangeText={(text) => activeMessage=text}
        onSubmitEditing={(e) => {
          const value = e.target.value;
          const nextMessage = buildMessage(value, true)
          const nextMessages = [...messages, nextMessage];

          setMessages(nextMessages);

          ref.current.value = '';
        }}
        style={{
          flex: 1,
          // input
          borderColor: '#999999',
          borderWidth: 1,
          paddingTop: 4,
          paddingBottom: 5,
          paddingHorizontal: 12,
          borderRadius: 15,
          marginRight: 5,

          // text
          fontSize: 16,
          // fontWeight: 'bold',
        }}
      />
      <Button 
        title='Send'
        onPress={() => {
          console.log(activeMessage);
          const value = activeMessage;
          const nextMessage = buildMessage(value, true)
          const nextMessages = [...messages, nextMessage];

          setMessages(nextMessages);

          ref.current.value = '';
        }} 
      />
    </View>
  );
}

function Container({ children }) {
  return (
    <View style={{
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
    }}>
      {children}
    </View>
  )
}

function buildMessage(string, isUser = true) {
  return {
    isUser,
    message: string,
    date: +new Date()
  }
}

function getLastMessage(messages) {
  return messages[messages.length - 1];
}

function createPrompt(messages) {
  let prompt = prompt_starter + '\n\n';
  for (let i = 0; i < messages.length; i++) {
    let obj = messages[i];
    if (obj.isUser) {
      prompt += 'Client: ' + obj.message + '\n';
    } else {
      prompt += 'Aurora: ' + obj.message + '\n';
    }
  }
  prompt += '\nAurora: '
  return prompt
}

export default App;